<!-- 添加 -->
<template>
	<div class="add-button">
		<el-button @click="clickAdd" type="primary" size="small" icon="el-icon-plus">{{$t('button.add')}}</el-button>
		<el-dialog :title="title" @close="addClose" :close-on-click-modal="false" width="500px" :visible.sync="addDialog.addDialog">
			<el-aside style="max-height:550px;width:100%;">
				<el-form :rules="rules" :model="listLi" ref="ruleForm">
					<el-form-item v-for="(v,key) in listLi" :key="key" :label="v.label" :prop="key" :label-width="formLabelWidth">
						<el-input v-if="v.type == 'input'" v-model="v.model" auto-complete="off"></el-input>
						<!-- <el-select style="width:100%;" v-if="v.type == 'dropDown'" v-model="v.model" placeholder="请选择">
							<el-option v-for="(item,index) in v.children" :key="index" :label="item" :value="item"></el-option>
						</el-select> -->
						<template v-if="v.type == 'dropDown'">
							<el-radio v-model="v.model" v-for="(item,index) in v.children" :key="index" :label="index">{{ item }}</el-radio>
						</template>
						<el-input v-if="v.type == 'textarea'" type="textarea" :autosize="{ minRows: 2, maxRows: 4}" v-model="v.model"></el-input>
						<el-input-number style="width:100%;" :min="0" v-if="v.type == 'inputNumber'" v-model="v.model" controls-position="right"></el-input-number>
					</el-form-item>
				</el-form>
			</el-aside>
			<div slot="footer" class="dialog-footer">
				<el-button size="small" @click="addCancel">{{$t('button.cancel')}}</el-button>
				<el-button size="small" type="primary" @click="addConfirm('ruleForm')">{{$t('button.confirm')}}</el-button>
			</div>	
		</el-dialog>
	</div>
</template>

<script>
import store from '@/store'
var ParentFn = ()=>import('../conmon/js/commo.js');
var Parent;
export default {
	name: 'clickButtonAdd',
	data() {
		return {
			dialogTableVisible: false
		}
	},
	created() {
		ParentFn().then(value =>{
			Parent = new value.default.ParentFn();
		})
	},
	mounted() {
		console.log(this.addDialog)
		
	},
	methods: {
		//确认
		addConfirm(formName){
			this.$refs[formName].validate((valid) => {
				if (valid) {
					this.$emit('addConfirm',this.listLi);
				} else {
					return false;
				}
			});
		},
		//点击添加按钮
		clickAdd(){
			this.$emit('clickAddInfo');
		},
		//取消
		addCancel(){
			this.$emit('addInfoCancel');
		},
		addClose(){
			this.$refs.ruleForm.resetFields();
			for(var key in this.listLi){
				if(this.listLi[key].type == 'dropDown') {
					this.listLi[key].model = 0;
				} else {
					this.listLi[key].model = '';
				}
			}
		}
	},
	props: {
		listLi:Object,
		title:String,
		rules:Object,
		formLabelWidth:String,
		menuId:String,
		menuParentId:String,
		//单纯的boolean在子组件修改会报错，所以这里接收一个对象
		addDialog:[Object,Boolean]
	},
	store
}
</script>

<style scoped>

</style>


